.header {
  display: flex;
  flex-direction: row;
  padding-top: 24px;
  padding-bottom: 24px;
  background-color: #3f51b5;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  z-index: 500;
}

.burger-open {
  width: 20px;
  height: auto;
  cursor: pointer;
  margin-right: 16px;
}

.burger-close {
  width: 20px;
  height: auto;
  z-index: 50;
  cursor: pointer;
  margin-right: 16px;
}

.burger__menu {
  display: flex;
  flex-direction: column;
  margin-right: 64px;
  position: absolute;
  top: 120px;
  margin-right: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  animation-name: popupAnimation;
  animation-duration: 1.5s;
  animation-iteration-count: 1;
  z-index: 50;
}

.burger__menu__element {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.78;
  letter-spacing: normal;
  color: #ffffff;
  margin-left: 32px;
  font-size: 18px;
  margin-bottom: 40px;
}

.burger__menu__element__link {
  width: 100%;
  text-decoration: none;
  color: white;
}

.overlay {
  position: fixed;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}

.logo__link {
  text-decoration: none;
  color: white;
}

.logo__link__img {
  width: 110px;
  height: 32px;
  margin-left: 64px;
}

.menu {
  display: flex;
  flex-direction: row;
  margin-right: 64px;
}

.menu__element {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.78;
  letter-spacing: normal;
  color: #ffffff;
  margin-left: 32px;
  font-size: 18px;
}

@keyframes popupAnimation {
  0% {
    left: -200px
  }

  100% {
    left: 0
  }
}

@media (max-width: 800px) {
  .header {
    padding-top: 16px;
    padding-bottom: 16px;
  }

  .logo__link__img {
    width: auto;
    height: 24px;
    margin-left: 16px;
  }
}