/*banner*/
.banner {
  display: flex;
  flex-direction: column;
  background-color: #3f51b5;
  font-family: 'Exo 2', 'Calibri', sans-serif;
}

.banner__separator {
  height: 132px;
}

.banner__content {
  background-image: url("../../Assets/kropki-baner-kompresja.svg");
  background-size: cover;
  background-color: #3f51b5;
}

.banner__content__header {
  margin-top: 152px;
  padding-left: 176px;
  margin-bottom: 8px;
  font-size: 64px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  color: #ffffff;
}

.banner__content__subheader {
  margin-bottom: 48px;
  font-size: 32px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  padding-left: 176px;
  line-height: 1.5;
  letter-spacing: normal;
  color: #ffffff;
}

.banner__content__contact {
  text-decoration: none;
  padding-top: 12px;
  margin-left: 176px;
  border: 0;
  padding-bottom: 12px;
  width: 304px;
  display: flex;
  font-size: 18px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: normal;
  text-align: center;
  color: #1f2961;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
  background-color: #ffffff;
  margin-bottom: 168px;
  border: none;
  font-family: inherit;
}

/*Brzmi*/

.brzmi {
  display: flex;
  flex-direction: column;
  background-image: url("../../Assets/kropki-dol-kompresja.svg");
  background-size: cover;
  font-family: 'Exo 2', 'Calibri', sans-serif;
  align-items: center;
  margin-bottom: 144px;
}

.brzmi__header {
  margin-top: 96px;
  margin-bottom: 80px;
  font-size: 64px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  text-align: center;
  color: #3f51b5;
}

.brzmi__contact {
  text-decoration: none;
  color: white;
  font-size: 18px;
  border: none;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  border-radius: 4px;
  background-color: #3f51b5;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-right: 24px;
  padding-left: 24px;
  max-width: 303px;
  margin-bottom: 92px;
}

/*main*/

.product {
  display: flex;
  flex-direction: row;
  font-family: "Exo 2", "Calibri", sans-serif;
  background-color: #f6f6f6;
}

.product__img,
.product-2__img {
  width: 50%;
}

.product__description {
  width: 50%;
  padding-bottom: 40px;
  padding-top: 64px;
}

.product__header {
  font-size: 30px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #3f51b5;
  margin-bottom: 32px;
  padding-left: 48px;
  padding-right: 176px;
}

.product__content {
  padding-left: 48px;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.78;
  letter-spacing: normal;
  color: #1f2961;
  padding-right: 176px;
}

.product-2 {
  display: flex;
  flex-direction: row;
  font-family: "Exo 2", "Calibri", sans-serif;
  background-color: #f2f3f5;
}

.product-2__description {
  width: 50%;
  padding-bottom: 40px;
  padding-top: 64px;
  background-image: url("../../Assets/tlo-2.svg");
  background-repeat: no-repeat;
}

.product-2__header {
  font-size: 30px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #3f51b5;
  margin-bottom: 32px;
  padding-left: 176px;
  padding-right: 48px;
}

.product-2__content {
  padding-left: 176px;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.78;
  letter-spacing: normal;
  color: #1f2961;
  padding-right: 48px;
}

.product__description-1 {
  width: 50%;
  padding-bottom: 40px;
  padding-top: 64px;
  background-image: url("../../Assets/t-o-3.svg");
  background-repeat: no-repeat;
}

.product__description-2 {
  width: 50%;
  padding-bottom: 40px;
  padding-top: 64px;
}

@media (max-width: 1280px) {
  .product__description {
    padding-bottom: 24px;
    padding-top: 40px;
  }

  .product__header {
    font-size: 26px;
    margin-bottom: 24px;
    padding-left: 32px;
    padding-right: 144px;
  }

  .product__content {
    font-size: 16px;
    padding-left: 32px;
    padding-right: 144px;
  }

  .product-2__description {
    padding-bottom: 24px;
    padding-top: 40px;
  }

  .product-2__header {
    font-size: 26px;
    margin-bottom: 24px;
    padding-left: 144px;
    padding-right: 32px;
  }

  .product-2__content {
    font-size: 16px;
    padding-left: 144px;
    padding-right: 32px;
  }

  .product__description-1 {
    padding-bottom: 24px;
    padding-top: 40px;
  }

  .product__description-2 {
    padding-bottom: 24px;
    padding-top: 40px;
  }
}

@media (max-width: 1080px) {

  .product__header {
    font-size: 20px;
    margin-bottom: 24px;
    padding-left: 32px;
    padding-right: 80px;
  }

  .product__content {
    font-size: 14px;
    padding-left: 32px;
    padding-right: 80px;
  }

  .product-2__header {
    font-size: 20px;
    margin-bottom: 24px;
    padding-left: 80px;
    padding-right: 32px;
  }

  .product-2__content {
    font-size: 14px;
    padding-left: 80px;
    padding-right: 32px;
  }
}

@media (max-width: 800px) {
  .product-2__content {
    font-size: 16px;
    padding-left: 16px;
    padding-right: 16px;
    line-height: 2;
  }

  .product__description-1 {
    padding-bottom: 32px;
    background-image: none;
    padding-top: 32px;
    width: 100%;
  }
}

@media (max-width: 800px) {
  .banner__separator {
    height: 108px;
  }

  .banner__content__header {
    font-size: 40px;
    margin-top: 40px;
    padding-left: 16px;
  }

  .banner__content__subheader {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    margin-bottom: 56px;
    padding-left: 16px;
  }

  .banner__content__contact {
    margin-left: 0;
    margin: 0 auto;
    margin-bottom: 56px;
    width: calc(100% - 32px);

  }

  .product {
    flex-direction: column-reverse;
  }

  .product__img,
  .product-2__img {
    width: 100%;

  }

  .product__description {
    padding-bottom: 32px;
    padding-top: 32px;
    width: 100%;
  }

  .product__header {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 16px;
    padding-left: 16px;
    padding-right: 16px;
    line-height: 1.78;
  }

  .product__content {
    padding-left: 16px;
    padding-right: 16px;
    font-size: 16px;
    line-height: 2;
  }

  .product-2 {
    flex-direction: column;

  }

  .product-2__description {
    padding-bottom: 32px;
    background-image: none;
    padding-top: 32px;
    width: 100%;
  }

  .product-2__header {
    padding-left: 16px;
    padding-right: 16px;
    font-weight: 600;
    margin-bottom: 16px;
    line-height: 1.78;
  }

  .product__description-2 {
    padding-bottom: 32px;
    padding-top: 32px;
    width: 100%;
  }
}

@media (max-width: 600px) {
  .brzmi__header {
    font-size: 40px;
  }
}